import {
    Component,
    h,
    Prop,
    State,
    Element,
    Watch,
    EventEmitter,
    Event,
} from '@stencil/core';
import {
    DateFormat,
    DateRangeStrings,
    Duration,
    MIN_DATE,
} from '../../../types';
import { TimeDateUtil } from '../../../utils/time-date.util';
import { fetchTranslations } from '../../../utils/translation';
import { TimeDatePresentationUtil } from '../../../utils/time-date-presentation.util';
import moment from 'moment';
import { SelectOption } from '../../../types/select';

@Component({
    tag: 'at-custom-time-range',
    shadow: false,
})
export class AtCustomTimeRangeComponent {
    /**
     * Whether time selection is enabled in addition to date selection
     */
    @Prop()
    can_set_time: boolean = true;

    /**
     * Minimum number of seconds for the time range
     */
    @Prop()
    min_seconds: number = 60;

    /**
     * Minimum selectable date
     */
    @Prop({ mutable: true }) min_date: Date = MIN_DATE;
    @Watch('min_date')
    validateMinDate(newValue: Date, oldValue: Date) {
        if (newValue && oldValue && newValue.getTime() === oldValue.getTime())
            return;
        this.min_date = this.floorMinDate(newValue);
    }

    /**
     * Maximum selectable date
     */
    @Prop({ mutable: true }) max_date: Date = new Date(Date.now());
    @Watch('max_date')
    validateMaxDate(newValue: Date, oldValue: Date) {
        if (newValue && oldValue && newValue.getTime() === oldValue.getTime())
            return;
        this.max_date = this.ceilingMaxDate(newValue);
    }

    /**
     * Default value for the from date
     */
    @Prop({ mutable: true }) default_from_date: Date;
    @Watch('default_from_date')
    validateDefaultFromDate(newValue: Date, oldValue: Date) {
        if (newValue && oldValue && newValue.getTime() === oldValue.getTime())
            return;
        this.default_from_date = this.floorMinDate(newValue);
        this.from_date_value = this.default_from_date;
    }

    /**
     * Default value for the to date
     */
    @Prop({ mutable: true }) default_to_date: Date;
    @Watch('default_to_date')
    validateDefaultToDate(newValue: Date | null, oldValue: Date | null) {
        if (newValue && oldValue && newValue.getTime() === oldValue.getTime())
            return;
        this.default_to_date = this.ceilingMaxDate(newValue);
        this.to_date_value = this.default_to_date
            ? this.default_to_date
            : this.max_date;
    }

    /**
     * Current value of the from date
     */
    @Prop({ mutable: true }) from_date_value: Date;
    @Watch('from_date_value')
    validateFromDateValue(newValue: Date | null, oldValue: Date | null) {
        if (newValue && oldValue && newValue.getTime() === oldValue.getTime())
            return;
        if (this.from_date_value === undefined)
            this.from_date_value = new Date();
        if (
            this.from_date_value.toISOString() !==
            this.floorMinDate(newValue).toISOString()
        ) {
            this.from_date_value = newValue && this.floorMinDate(newValue);
        }
        this.setFromDateAndTime(this.from_date_value);
        this.updateMinMaxFlags();
    }

    /**
     * Current value of the to date
     */
    @Prop({ mutable: true }) to_date_value: Date;
    @Watch('to_date_value')
    validateToDateValue(newValue: Date | null, oldValue: Date | null) {
        if (newValue && oldValue && newValue.getTime() === oldValue.getTime())
            return;

        if (this.to_date_value === undefined) this.to_date_value = new Date();
        if (
            this.to_date_value.toISOString() !==
            this.ceilingMaxDate(newValue).toISOString()
        ) {
            this.to_date_value = newValue && this.ceilingMaxDate(newValue);
        }
        this.setToDateAndTime(this.to_date_value);
        this.updateMinMaxFlags();
    }

    /**
     * Whether to lock the end date to the current time
     */
    @Prop({ mutable: true }) lock_end_date_to_now = false;

    @State() isFromMinDay = true;
    @State() isToMaxDay = true;
    @State() isFromMaxDay = false;
    @State() isToMinDay = false;
    @State() toDate: string;
    @State() toTime: string;
    @State() fromDate: string;
    @State() fromTime: string;
    @State() translations: any;
    @State() setDateNowSwitch: HTMLAtToggleSwitchElement;
    @State() toTimePickerEl: HTMLAtSelectElement;
    @Element() el;

    async componentWillLoad() {
        this.translations = await fetchTranslations(this.el);
    }

    componentWillRender() {
        if (!this.fromDate && !this.fromTime) {
            this.from_date_value = this.default_from_date
                ? this.default_from_date
                : this.min_date;
        }
        if (!this.toDate && !this.toTime) {
            this.to_date_value = this.default_to_date
                ? this.default_to_date
                : this.max_date;
        }
    }

    clearSelection(): void {
        this.from_date_value = this.default_from_date
            ? this.default_from_date
            : this.min_date;
        this.to_date_value = this.default_to_date
            ? this.default_to_date
            : this.max_date;
    }

    private ceilingMaxDate(date: Date): Date {
        return TimeDateUtil.ceilingDateByTimeUnit(date, Duration.HOURS);
    }

    private floorMinDate(date: Date): Date {
        return TimeDateUtil.floorDateByTimeUnit(date, Duration.HOURS);
    }

    private getCustomToDateTime(): Date {
        return TimeDatePresentationUtil.buildDateFromStrings(
            this.toDate,
            this.toTime,
        );
    }

    private getCustomFromDateTime(): Date {
        return TimeDatePresentationUtil.buildDateFromStrings(
            this.fromDate,
            this.fromTime,
        );
    }

    private updateMinMaxFlags(): void {
        const to = this.getCustomToDateTime();
        this.isToMinDay = TimeDateUtil.isSameDateByUnit(
            to,
            this.min_date,
            Duration.DAYS,
        );
        this.isToMaxDay = TimeDateUtil.isSameDateByUnit(
            to,
            this.max_date,
            Duration.DAYS,
        );

        const from = this.getCustomFromDateTime();
        this.isFromMinDay = TimeDateUtil.isSameDateByUnit(
            from,
            this.min_date,
            Duration.DAYS,
        );
        this.isFromMaxDay = TimeDateUtil.isSameDateByUnit(
            from,
            this.max_date,
            Duration.DAYS,
        );
    }

    protected setDateNow(): void {
        const toggleValue = this.setDateNowSwitch.value;
        if (toggleValue) {
            this.lock_end_date_to_now = true;
            const date = this.ceilingMaxDate(new Date(Date.now()));
            this.to_date_value = date;
        } else {
            this.lock_end_date_to_now = false;
        }
    }

    private setToDateAndTime(toDate: Date) {
        this.toDate = TimeDatePresentationUtil.getFormattedDate(
            toDate,
            'YYYY-MM-DD' as DateFormat,
        );
        this.toTime = TimeDatePresentationUtil.getFormattedDate(
            toDate,
            'hh:mm a' as DateFormat,
        );
    }

    private setFromDateAndTime(fromDate: Date) {
        this.fromDate = TimeDatePresentationUtil.getFormattedDate(
            fromDate,
            'YYYY-MM-DD' as DateFormat,
        );
        this.fromTime = TimeDatePresentationUtil.getFormattedDate(
            fromDate,
            'hh:mm a' as DateFormat,
        );
    }

    get getFromTimes() {
        this.updateMinMaxFlags();

        if (this.isFromMinDay) {
            return TimeDatePresentationUtil.getTimeOptions(
                TimeDatePresentationUtil.getFormattedDate(
                    this.min_date,
                    DateFormat.HOURS_ONLY,
                ),
            );
        } else if (this.isFromMaxDay) {
            const fromMaxTime = TimeDatePresentationUtil.getFormattedDate(
                TimeDateUtil.shiftDateByUnit(this.max_date, -1, Duration.HOURS),
                DateFormat.HOURS_ONLY,
            );
            return TimeDatePresentationUtil.getTimeOptions(
                undefined,
                fromMaxTime,
            );
        } else {
            return TimeDatePresentationUtil.getTimeOptions();
        }
    }

    get getToTimes(): SelectOption[] {
        this.updateMinMaxFlags();

        if (this.isToMinDay) {
            const toMaxTime = TimeDatePresentationUtil.getFormattedDate(
                TimeDateUtil.shiftDateByUnit(this.min_date, 1, Duration.HOURS),
                DateFormat.HOURS_ONLY,
            );
            return TimeDatePresentationUtil.getTimeOptions(toMaxTime);
        } else if (this.isToMaxDay) {
            return TimeDatePresentationUtil.getTimeOptions(
                undefined,
                TimeDatePresentationUtil.getFormattedDate(
                    this.max_date,
                    DateFormat.HOURS_ONLY,
                ),
            );
        } else {
            return TimeDatePresentationUtil.getTimeOptions();
        }
    }

    /**
     * Emitted when the user cancels the time range selection
     */
    @Event() atuiCancel: EventEmitter<null>;
    /**
     * Emitted when the user submits the time range selection
     */
    @Event() atuiSubmit: EventEmitter<DateRangeStrings>;

    handleCancel(): void {
        this.atuiCancel.emit();
    }

    handleSubmit() {
        if (moment(this.from_date_value).isBefore(moment(this.to_date_value))) {
            this.atuiSubmit.emit({
                from: TimeDatePresentationUtil.buildDateFromStrings(
                    this.fromDate,
                    this.fromTime,
                ).toISOString(),
                to: TimeDatePresentationUtil.buildDateFromStrings(
                    this.toDate,
                    this.toTime,
                ).toISOString(),
                lockEndDateToNow: this.lock_end_date_to_now,
            });
        }
    }

    render() {
        return (
            <div class="flex w-fit flex-col gap-8 border border-gray-300">
                <h5 class="text-h5 text-dark p-12 font-medium">
                    {this.translations.ATUI.TIME.SELECT_ABSOLUTE_TIME}
                </h5>
                <div class="flex flex-row gap-16 px-12">
                    <div class="flex-fill flex flex-col gap-8">
                        <at-datepicker
                            label={this.translations.ATUI.TIME.FROM_DATE}
                            value={this.from_date_value}
                            min_date={this.min_date}
                            max_date={this.max_date}
                            onAtuiChange={(event) => {
                                this.from_date_value = event.detail;
                            }}
                        />
                        {this.can_set_time && (
                            <div>
                                <label>
                                    {this.translations.ATUI.TIME.FROM_TIME}
                                </label>
                                <div>
                                    <at-select
                                        options={this.getFromTimes}
                                        value={this.fromTime}
                                        onAtuiChange={(event) => {
                                            this.from_date_value =
                                                TimeDatePresentationUtil.buildDateFromStrings(
                                                    this.fromDate,
                                                    event.detail,
                                                );
                                        }}
                                    ></at-select>
                                </div>
                            </div>
                        )}
                    </div>
                    <div class="flex-fill flex flex-col gap-8">
                        <at-datepicker
                            label={this.translations.ATUI.TIME.TO_DATE}
                            value={this.to_date_value}
                            min_date={this.min_date}
                            max_date={this.max_date}
                            onAtuiChange={(event) => {
                                this.to_date_value = event.detail;
                            }}
                        />
                        {this.can_set_time && (
                            <div>
                                <label>
                                    {this.translations.ATUI.TIME.TO_TIME}
                                </label>
                                <div>
                                    <at-select
                                        options={this.getToTimes}
                                        value={this.toTime}
                                        disabled={this.lock_end_date_to_now}
                                        ref={(el) => (this.toTimePickerEl = el)}
                                        onAtuiChange={(event) => {
                                            this.to_date_value =
                                                TimeDatePresentationUtil.buildDateFromStrings(
                                                    this.toDate,
                                                    event.detail,
                                                );
                                        }}
                                    ></at-select>
                                </div>
                            </div>
                        )}
                    </div>
                </div>
                <div class="align-center my-8 flex flex-row justify-between gap-8 rounded-md px-12">
                    <div class="flex flex-col">
                        <label class="c-form__label">
                            {
                                this.translations.ATUI.TIME
                                    .SET_END_DATE_AND_TIME_TO_NOW
                            }
                        </label>
                        <span class="text-med text-xs">
                            {
                                this.translations.ATUI.TIME
                                    .SET_END_DATE_AND_TIME_TO_NOW_DESCRIPTION
                            }
                        </span>
                    </div>
                    <at-toggle-switch
                        value={this.lock_end_date_to_now}
                        onChange={() => this.setDateNow()}
                        ref={(el) => (this.setDateNowSwitch = el)}
                    ></at-toggle-switch>
                </div>
                <div class="flex flex-row justify-between p-8">
                    <at-button
                        type="secondaryOutline"
                        data-name="custom-time-range-clear"
                        label={this.translations.ATUI.CLEAR_SELECTION}
                        onClick={() => this.clearSelection()}
                    ></at-button>
                    <div class="flex flex-row gap-8">
                        <at-button
                            type="secondaryOutline"
                            data-name="custom-time-range-cancel"
                            label={this.translations.ATUI.CANCEL}
                            onClick={() => this.handleCancel()}
                        ></at-button>
                        <at-button
                            data-name="custom-time-range-apply"
                            label={this.translations.ATUI.APPLY}
                            onClick={() => this.handleSubmit()}
                        ></at-button>
                    </div>
                </div>
            </div>
        );
    }
}
